<template>
  <div>
  <el-table
  :data="tableData5.slice((currentPage-1)*pagesize,currentPage*pagesize)"

    style="width: 100%">
    <el-table-column type="expand">
      <template slot-scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="正文">
            <span>{{ props.row.content}}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      label="标题"
      prop="title">
    </el-table-column>
    <el-table-column
      label="方法一"
      prop="cate_name1">
    </el-table-column>
    <el-table-column
      label="方法二"
      prop="cate_name2">
    </el-table-column>
    <el-table-column
      label="方法三"
      prop="cate_name3">
    </el-table-column>
    <el-table-column
      label="两者交集"
      prop="jiaoji">
    </el-table-column>
  </el-table>


    <el-pagination class="middle_con"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      background
      :page-sizes="[1,3,5,10,15,20]"  
      :page-size="pagesize"         
      layout="total, sizes,  prev, pager, next, jumper" 
      :total="tableData5.length">   
    </el-pagination>

  </div>
</template>



<script>
import text_data1 from '@/excel/record1.json'
import text_data2 from '@/excel/record2.json'
import text_data3 from '@/excel/record3.json'
import title from '@/excel/title.json'
import content from '@/excel/content.json'
  export default {
    data() {
      return {
        currentPage:1, // 初始页
      pagesize:15,  // 初始每页的数据
      tableData5: [],
      }
    },
    created () {
    // 获取内容
    this.initListFn()
  },
  methods: {
    handleSizeChange: function (size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
        },
    initListFn(){
      // console.log(text_data)
      // console.log(text_data.length)

      for (var k = 0, length = text_data1.length; k < length; k++) {

        let addForm={}  
        let fix12=[]      


        addForm.cate_name1=text_data1[k].join(' ')
        addForm.cate_name2=text_data2[k].join(' ')
        addForm.cate_name3=text_data3[k].join(' ')
        for (let add1 = 0, length = text_data1[k].length; add1 < length; add1++) {
          if (text_data2[k].includes(text_data1[k][add1]) || text_data3[k].includes(text_data1[k][add1])){
            fix12.push(text_data1[k][add1])
          }
        }

        addForm.jiaoji=fix12.join(' ')
        addForm.title=title[k]

        addForm.content=content[k]

        this.tableData5.push(addForm)

      }
    //   for (let i=this.tableData5.length-1; i>=0; i--) {
    //     let rIndex = Math.floor(Math.random()*(i+1));

    //     // console.log(i, rIndex);
    //     let temp = this.tableData5[rIndex];
    //     this.tableData5[rIndex] = this.tableData5[i];
    //     this.tableData5[i] = temp;
    // }

    }
}}
  
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .middle_con {
    margin-left:50px;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-left: 20%;
    margin-bottom: 0;
    width: 60%;
  }
</style>